  const { jobs } = useLoaderData();
  console.log("Loaded Jobs:");
  console.log(jobs);
  const [sortingProperty, setSortingProperty] = useState("id");
  //const [sortedJobs, setSortedJobs] = useState(tempJobs);
  /* useEffect(() => {
    // sorting here by call function sort()
    sortedJobs = tempJobs.sort(
      (job_1, job_2) => job_1.sortingProperty - job_2.sortingProperty
    );
  }, [sortedJobs]); */
